.footer{background-color:var(--text-primary);color:var(--white);padding:1rem 0 1rem}.footer .footerContent{display:flex;flex-direction:row;justify-content:start;align-items:start}.footer .logoContainer{display:flex;justify-content:flex-start;align-items:start;margin-bottom:3rem}.footer .balaoContainer{display:flex;flex-direction:row;gap:1rem;text-decoration:none;transition:transform .3s ease}.footer .balaoContainer:hover{transform:scale(1.05)}.footer .balao{margin-top:-8rem;height:100%}.footer .balao img{width:100%;height:100%;object-fit:contain}@media(max-width: 1200px){.footer .balao{margin-top:-3rem;margin-left:0}}@media(max-width: 768px){.footer .balao{margin-top:-3rem;margin-left:0}}.footer .footerBottom{width:100%;padding-top:2rem;display:flex;justify-content:center;align-items:center;flex-wrap:wrap;gap:3rem}.footer .footerBottom .copyright p,.footer .footerBottom .credentials p{margin:0;font-size:.9rem;color:hsla(0,0%,100%,.8)}.footer .footerBottom .credentials{font-style:italic}@media(max-width: 768px){.footer{padding:2rem 0 1rem}.footer .footerBottom{flex-direction:column;text-align:center;gap:1rem}}